import { Component, OnInit, ViewChild } from '@angular/core';
import { SocketconfigService } from '../service/socketconfig.service';

import { MdlDialogComponent, MdlDialogReference, MdlSnackbarService } from '@angular-mdl/core';
import { FormControl, Validators } from '@angular/forms';
import * as io from "socket.io-client";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  myDate: any;
  public soketIpValue: FormControl = new FormControl('');
  public usernameValue: FormControl = new FormControl('');
  public passwordValue: FormControl = new FormControl('');
  public socket: SocketIOClient.Socket; 

  @ViewChild('SoketIpDialog', { static: true }) private SoketIpDialog: MdlDialogComponent;
  @ViewChild('MapCategoriesDialog',{static: true }) private MapCategoriesDialog:MdlDialogComponent;
  @ViewChild('LoginDialog', { static: true }) private LoginDialog: MdlDialogComponent;
  
  dialogRef: MdlDialogReference;
  categoriesList: any;
  selectedCategoriesList: any = [];
  
  kdsTypes = [
    {name:"Expeditor KDS (Expo)", value:"expo"},
    {name:"Preparation KDS (Prep)", value:"prep"},
    {name:"KDS", value:"kds"}
  ]
  selectedKdsType = localStorage.getItem('selectedKdsType') != null ? localStorage.getItem('selectedKdsType') : 'expo';


  constructor(private SocketconfigService: SocketconfigService, private mdlSnackbarService: MdlSnackbarService) 
  { 
    // let ipAddress = SocketconfigService.getIpaddress();
    // console.log("SocketconfigService.getIpaddress()", ipAddress);
    // let socketServerUrl = `http://${ipAddress}:7000`;
    // this.socket = io(socketServerUrl);

    this.socket = SocketconfigService.getSocketConnection();

    this.socket.off("get-Categories");
  }
  flag = 0;
  address: any;

  activeOrderTab = 'restaurant'
  isLoggedIn: boolean = false;


  switchOrderTab() {
    this.activeOrderTab = this.activeOrderTab === 'restaurant' ? 'online' : 'restaurant';
  }

  ngOnInit() {

    setInterval(() => {   //replaced function() by ()=>
      this.myDate = new Date();
    }, 1000);

    if (localStorage.getItem('ipAddress') != null) {
      this.address = localStorage.getItem('ipAddress');
      this.flag = 1;
    }

    console.log("this.flag status", this.flag)

    this.saveCategoriesToKDS()
  }

  /** Soket Ip*/
  SoketIpDialogShow(dialogRef) {
  }

  SoketIpDialogHide() {
  }

  SoketIpDialogshow() {
    this.SoketIpDialog.show();
  }

  changeSocketIp() {
    if (this.soketIpValue.value != null && this.soketIpValue.value != '') {
      this.SocketconfigService.selectipaddress(this.soketIpValue.value);
      this.flag = 1;
    } else {
      this.showSnackbar();
    }
    this.SoketIpDialog.close();
    this.ngOnInit();
  }



  showSnackbar() {
    this.mdlSnackbarService.showSnackbar({
      message: 'IP Address field should not be Empty OR  Please enter correct IP Address ',
      action: {
        handler: () => {
          // this.mdlSnackbarService.showToast('You hit the ok Button');
        },
        text: 'OK'
      }
    });
  }



  /** Map Categorys */
  MapCategoriesDialogShow(dialogRef) {
  }

  MapCategoriesDialogHide() {
    this.selectedKdsType = localStorage.getItem('selectedKdsType') != null ? localStorage.getItem('selectedKdsType') : 'expo';
  }

  MapCategoriesDialogshow() {
    this.getCategoriesResponse();
    this.MapCategoriesDialog.show();
  }


  getCategoriesResponse() {
    this.socket.emit("get-categories");

    this.selectedKdsType = localStorage.getItem('selectedKdsType') != null ? localStorage.getItem('selectedKdsType') : 'expo';
    this.selectedCategoriesList = localStorage.getItem('selectedCategoriesList')!= null ? JSON.parse(localStorage.getItem('selectedCategoriesList')) : [];

    this.socket.on("get-categories-response", resposedata => {
      this.categoriesList = [];
      if (resposedata.length != 0) {
        resposedata.forEach(category => {
          if(this.selectedCategoriesList.some(item => item === category.menu_id) ){
            category.showCategoryFlag = true
          } else{
            category.showCategoryFlag = false
          }
        });

        this.categoriesList = resposedata;
      }
    });

    console.log(this.categoriesList);
  }

  saveCategoriesToKDS() {
    console.log(this.categoriesList);
    this.selectedCategoriesList = [];

    if(this.categoriesList){
      this.categoriesList.forEach(category => {
        if (category.showCategoryFlag === true) {
          this.selectedCategoriesList.push(category.menu_id)
        }
      });
    }
      

    localStorage.removeItem('selectedKdsType');
    localStorage.setItem('selectedKdsType', this.selectedKdsType);

    localStorage.removeItem('selectedCategoriesList');
    localStorage.setItem('selectedCategoriesList',JSON.stringify(this.selectedCategoriesList));

    localStorage.removeItem('categoriesList');
    localStorage.setItem('categoriesList',JSON.stringify(this.categoriesList));
    
    this.mdlSnackbarService.showSnackbar({
      message: "KDS Configuration Updated!"
    });

    this.MapCategoriesDialog.close();

    // Refresh the app to apply new configuration
    window.location.reload();
  }


  changeKDSType(event: any) {
    this.selectedKdsType = event.target.value
    console.log(event.target.value)
  }

  selectAllCategories(){
    this.categoriesList.forEach(category => {
      category.showCategoryFlag = true
    });
  }

  /** Login Dialog */
  LoginDialogShow(dialogRef) {
  }

  LoginDialogHide() {
    this.usernameValue.reset();
    this.passwordValue.reset();
  }

  LoginDialogshow() {
    this.LoginDialog.show();
  }

  handleLogin() {
    if (this.usernameValue.value && this.passwordValue.value) {
      // TODO: Implement actual login logic here
      this.isLoggedIn = true;
      this.LoginDialog.close();
      this.mdlSnackbarService.showSnackbar({
        message: 'Login successful!'
      });
    } else {
      this.mdlSnackbarService.showSnackbar({
        message: 'Please enter both username and password',
      });
    }
  }

}
