import { Injectable } from '@angular/core';
import * as io from "socket.io-client";
import { MdlSnackbarService } from "@angular-mdl/core";


@Injectable({
  providedIn: 'root'
})
export class SocketconfigService {

  private socket: SocketIOClient.Socket;

  constructor(private mdlSnackbarService: MdlSnackbarService) {
    let ipAddress = this.getIpaddress();
    let socketServerUrl = `http://${ipAddress}:7000`;
    console.log('socketServerUrl - ',socketServerUrl)
    // this.socket = io(socketServerUrl);

    try {
      this.socket = io(socketServerUrl);
    
      this.socket.on('connect', () => {
        console.log('Socket connected successfully');
        this.showSnackbar("KDS Connected Successfully!");
        this.socket.emit("Check-Exist-Order");
      });
    
      this.socket.on('connect_error', (error) => {
        console.error('Socket connection error:', error);
        this.showSnackbar("Network Connection Error! Reconnecting to POS Application");
      });
    
      this.socket.on('disconnect', () => {
        console.log('Socket disconnected');
        this.showSnackbar("KDS Disconnected! Please check POS or Network Connection");
      });
    } catch (error) {
      console.error('An error occurred while connecting to the socket server:', error);
      this.showSnackbar("Network Connection Error! Reconnecting to POS Application");
    }
  }
   
  getSocketConnection() {
    return this.socket;
  }

  selectipaddress(soketIpValue:any) {
    localStorage.setItem('ipAddress', soketIpValue);
    console.log("called")
  }

  getIpaddress() {
    console.log("get called")
    return localStorage.getItem('ipAddress');
  }

  showSnackbar(msg) {
    this.mdlSnackbarService.showSnackbar({
      message: msg,
      timeout: 10000
    });
  }

}
